<template>
  <div style="text-align: center">
    <h3>男女互相抽,请填写队员信息</h3>
    <span style="margin-right: 10px; ">队员总数:</span><el-input-number style="margin: 10px 0;" v-model="sum" :min="0" :max="40 "/><br>
    <el-button type="primary" @click="inputMember(sum)">确定</el-button>
  </div>
</template>

<script>
export default {
  name: "dealByGender",
  data() {
    return {
      sum: 0
    }
  },
  methods: {
    inputMember(sum) {
      sessionStorage.setItem("currentSum", sum);
      this.$router.push("/inputMemberBG");
    }
  }
}
</script>

